/**
* 双随机-执行结果组件
* @module 系统
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="objectName"
                :visible="show"
                @close="closeDialog"
                center
                top="50px"
                width="80%"
        >
            <div :key="item.checkValue" class="plan" v-for="item in objectTypearr" v-loading="loading">
                <div class="objecttype">
                    <table class="gridTable">
                        <tr>
                            <th>检查内容：</th>
                            <td colspan="4">
                                <span>{{item.checkValueName}}</span>
                            </td>
                        </tr>
                        <tr>
                            <th>检查结果：</th>
                            <td>
                                <span class="result-td">{{item.isGood==0?'不合格':(item.isGood==1?'合格':'暂无检查结果')}}</span>
                            </td>
                        </tr>
                        <tr v-if="item.isGood==0">
                            <th>处罚内容：</th>
                            <td>
                                <span class="bac-td">{{item.punishstr}}</span>
                            </td>
                        </tr>
                    </table>
                </div>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import {apiCheckResultGet} from "../../../api/case";
    import {apiOrgSearch} from "../../../api/system";

    export default {
        name: "edit",
        props: {
            objectType: {
                type: String,
                default: ""
            },
            objectName: {
                type: String,
                default: ""
            },
            editId: {
                type: String,
                default: ""
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "normal"
            },
            type: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                loading: true,
                punish: [],
                cities: [
                    {name: "警告", id: "warning"},
                    {name: "教育劝阻", id: "edu"},
                    {
                        name: "责令改正",
                        id: "change"
                    },
                    {name: "立案处罚", id: "case"}
                ],
                checked1: false,
                checked2: false,
                checked3: false,
                checked4: false,
                objectTypearr: [],
                checkArr: [
                    {
                        value: "month",
                        label: "每月检查"
                    },
                    {
                        value: "quarter",
                        label: "每季度检查"
                    },
                    {
                        value: "year",
                        label: "每年份检查"
                    }
                ],
                aaa: "",
                form: {},
                objArr: [],
                options: [],

                rules: {
                    objectType: [
                        {required: true, message: "请选择对象类型", trigger: "blur"}
                    ],
                    checkUnit: [
                        {required: true, message: "请选择对象类型", trigger: "blur"}
                    ],
                    objectName: [
                        {required: true, message: "请输入对象名称", trigger: "blur"},
                        {min: 1, message: "至少1个以上字符", trigger: "blur"}
                    ],
                    checkPerson: [
                        {required: true, message: "请输入检查人员", trigger: "blur"},
                        {min: 1, message: "至少1个以上数字", trigger: "blur"}
                    ],
                    objectAddress: [
                        {required: true, message: "请输入对象地址", trigger: "blur"},
                        {min: 2, message: "至少2个以上字符", trigger: "blur"}
                    ],
                    checkFreq: [
                        {required: true, message: "请选择检查频次", trigger: "blur"}
                    ],
                    isGood: [{required: true, message: "是否合格", trigger: "change"}],
                    remarks: [
                        {required: true, message: "请输入备注信息", trigger: "blur"}
                    ]
                },
                dataobj: {},
                dataArr: []
            };
        },
        methods: {
            closeDialog() {
                this.$emit("update:show", false);
            },
            clearValidate() {
                this.$refs.ruleForm.clearValidate();
            },

            //检查单位
            remoteMethod() {
                apiOrgSearch({keyWord: ""}).then(res => {
                    this.options = res.data;
                });
            },

            //弹框弹出时获取data给form
            initPage() {
                this.remoteMethod();
                //检查对象类型下拉
                this.$until.workBookApi("checkObjectType").then(res => {
                    this.objArr = res;
                });

                if (this.editId) {
                    //    编辑get接口
                    apiCheckResultGet(this.editId).then(res => {
                        if (res.data) {
                            res.data.forEach(item => {
                                item.isGood = String(item.isGood);
                                if (item.punish != "") {
                                    item.punish = item.punish.split(",").filter(item => item);

                                    let itemArr = item.punish;
                                    let punishArr = this.cities;
                                    let namestr = "";

                                    punishArr.forEach(item => {
                                        itemArr.forEach(item2 => {
                                            if (item.id == item2) {
                                                namestr += item.name + " ,";
                                            }
                                        });
                                    });
                                    item.punishstr = namestr.substr(0, namestr.length - 1);
                                } else {
                                    item.punish = [];
                                }
                            });
                            this.objectTypearr = res.data;
                            this.loading = false;
                        }
                    });
                } else {
                    this.form.objectType = "";
                    this.form.objectName = "";
                    this.form.objectTel = "";
                    this.form.objectAddress = "";
                    this.form.isCertificates = "";
                    this.form.checkUnit = "";
                    this.form.checkFreq = "";
                    this.form.remarks = "";
                }
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .plan {
        width: 100%;
        box-sizing: border-box;

        .objecttype {
            p {
                color: gray;
                padding: 10px;

                span {
                    color: #409eff;
                    font-weight: bold;
                }
            }

            width: 100%;
            margin-top: 10px;
            border-radius: 5px;
        }
    }

    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }

    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }

    .widthselect {
        width: 100%;
    }

    .result-td {
        color: green;
        font-weight: bold;
    }

    .bac-td {
        color: red;
    }
</style>


