/**
* 双随机-执行结果组件
* @module 系统
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-from :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                @row-click="seeFunction"
                @selection-change="handleSelectionChange"
                border
                size="mini"
                v-loading="loading"
        >
            <el-table-column align="center" label="检查对象" prop="objectName" show-overflow-tooltip/>

            <el-table-column align="center" label="检查单位" prop="checkUnit" show-overflow-tooltip>
                <template slot-scope="scope" v-if="scope.row.checkUnit">{{scope.row.checkUnit | checkUnit}}</template>
            </el-table-column>
            <el-table-column
                    align="center"
                    label="检查人员"
                    prop="checkPerson"
                    show-overflow-tooltip
                    width="180"
            />

            <el-table-column
                    align="center"
                    label="检查状态"
                    prop="checkStatus"
                    show-overflow-tooltip
                    width="100"
            >
                <template
                        slot-scope="scope"
                        v-if="scope.row.checkStatus"
                >{{scope.row.checkStatus | checkStatus}}
                </template>
            </el-table-column>
            <el-table-column
                    align="center"
                    label="计划检查时间"
                    prop="checkTime"
                    show-overflow-tooltip
                    width="150"
            >
                <template slot-scope="scope" v-if="scope.row.checkTime">{{scope.row.checkTime | formatDate}}</template>
            </el-table-column>
            <el-table-column
                    align="center"
                    label="检查开始时间"
                    prop="beginTime"
                    show-overflow-tooltip
                    width="150"
            >
                <template slot-scope="scope" v-if="scope.row.beginTime">{{scope.row.beginTime | formatDate}}</template>
            </el-table-column>
            <el-table-column
                    align="center"
                    label="最后检查时间"
                    prop="lastTime"
                    show-overflow-tooltip
                    width="150"
            >
                <template slot-scope="scope" v-if="scope.row.lastTime">{{scope.row.lastTime | formatDate}}</template>
            </el-table-column>

            <el-table-column align="center" label="操作" width="100">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="seeFunction(scope.row)"
                                class="el-icon-view btn-link"
                                type="success"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="检查情况是否合格操作" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="editFunction(scope.row)"
                                class="el-icon-edit btn-link"
                                type="primary"
                        ></el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getAccountList"
                class="page-css"
        />

        <!-- 编辑&添加弹框 -->
        <change-edit
                :editId="editObj.editId"
                :objectName="editObj.objectName"
                :objectType="editObj.objectType"
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.type"
                ref="child"
        />
        <!-- 查看 -->
        <see-content
                :editId="seeObj.editId"
                :objectName="seeObj.objectName"
                :objectType="seeObj.objectType"
                :show.sync="seeObj.show"
                :title="seeObj.title"
                :type="seeObj.type"
                ref="child"
        />
    </div>
</template>

<script>
    import {apiCheckResultSearch} from "../../../api/case";
    import {apiOrgSearch} from "../../../api/system";
    import LjPagination from "../../../components/LjPagination";
    import LjSearchFrom from "../../../components/LjSearchForm";
    import {formatDate, IFSEX} from "../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seecontent";

    let that;
    export default {
        name: "doResultPage",
        data() {
            return {
                loading: true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: "",
                    type: "",
                    editId: null,
                    objectType: "",
                    objectName: ""
                },
                seeObj: {
                    show: false,
                    title: "",
                    type: "",
                    editId: null
                },
                multipleSelection: [],
                //搜索组件数据
                searchObj: null,
                options: [],
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                total: 0,
                input: "",
                list: [
                    {
                        scId: "aaa1112222sss3ddd",
                        crimeTime: "2019-01-20",
                        crimeLocal: "XXX",
                        caseSource: "sssss",
                        HandlingUnit: "aa",
                        investigatorsName: "aa",
                        party: "aa",
                        partyType: "aa",
                        correctionMethod: "aa",
                        correctionResult: "aa",
                        caseStatus: "aa"
                    }
                ],
                rowdata: {}
            };
        },
        components: {
            changeEdit,
            LjPagination,
            LjSearchFrom,
            seeContent
        },

        methods: {
            //查询+搜索
            getAccountList() {
                this.loading = true;
                apiCheckResultSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });
            },

            //查看
            seeFunction(row) {
                this.seeObj.editId = row.planId;
                this.seeObj.objectType = row.objectType;
                this.seeObj.objectName = "查看" + row.objectName + "结果";
                this.seeObj.show = true;
            },

            //编辑
            editFunction(row) {
                this.editObj.editId = row.planId;
                this.editObj.objectType = row.objectType;
                this.editObj.objectName = "检查" + row.objectName + "结果";
                this.editObj.show = true;
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },

            //搜索
            searchList(data) {
                //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form.pageNum = 1;
                apiCheckResultSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                });
            }
        },
        beforeCreate: function () {
            that = this;
        },

        filters: {
            ifsex(val) {
                return IFSEX[val];
            },
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd hh:mm:ss");
            },
            checkUnit(val) {
                if (
                    that.options.find(item => {
                        return item.orgId == val;
                    })
                ) {
                    return that.options.find(item => {
                        return item.orgId == val;
                    }).orgName;
                }
            },
            checkStatus(val) {
                return val > 0 ? "已检查" : "未检查";
            }
        },
        mounted() {
        },

        created() {
            this.getAccountList();
            apiOrgSearch({keyWord: ""}).then(res => {
                this.options = res.data;
            });
            this.$until.workBookApi("checkObjectType").then(res => {
                // this.objArr = res;
            });
        }
    };
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px;
    }

    .btn-link {
        margin: 0 10px;
    }

    .page-css {
        margin-top: 10px;
    }
</style>
