/**
* 双随机-执行结果组件
* @module 系统
* @desc 添加修改
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="objectName"
                :visible="show"
                @close="closeDialog"
                center
                top="50px"
                width="80%"
        >
            <div :key="item.checkValue" class="plan" v-for="item in objectTypearr" v-loading="loading">
                <el-collapse accordion v-model="activeName">
                    <el-collapse-item :name="item.checkValue" :title="'检查内容： '+item.checkValueName">
                        <div class="checkwrop">
                            <table class="gridTable wroptable">
                                <tr>
                                    <th>检查内容：</th>
                                    <td colspan="4">
                                        <span class="check-content">{{item.checkValueName}}</span>
                                    </td>
                                </tr>
                                <tr>
                                    <th>检查结果：</th>
                                    <td>
                                        <el-radio-group v-model="item.isGood">
                                            <el-radio class="radio" label="1">合格</el-radio>
                                            <el-radio class="radio" label="0">不合格</el-radio>
                                        </el-radio-group>
                                    </td>
                                </tr>
                                <tr v-if="item.isGood==0">
                                    <th>处罚内容：</th>
                                    <td>
                                        <el-checkbox-group v-if="item.isGood==0" v-model="item.punish">
                                            <el-checkbox
                                                    :key="city.id"
                                                    :label="city.id"
                                                    v-for="city in cities"
                                            >{{city.name}}
                                            </el-checkbox>
                                        </el-checkbox-group>
                                    </td>
                                </tr>
                            </table>
                        </div>
                    </el-collapse-item>
                </el-collapse>
            </div>
            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiCheckResultAdd, apiCheckResultGet} from "../../../api/case";
    import {apiOrgSearch} from "../../../api/system";

    const cityOptions = [
        {name: "警告", id: "warning"},
        {name: "教育劝阻", id: "edu"},
        {
            name: "责令改正",
            id: "change"
        },
        {name: "立案处罚", id: "case"}
    ];

    export default {
        name: "edit",
        props: {
            objectType: {
                type: String,
                default: ""
            },
            objectName: {
                type: String,
                default: ""
            },
            editId: {
                type: String,
                default: ""
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "normal"
            },
            type: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                activeName: "1",
                loading: true,
                punish: [],
                cities: cityOptions,
                objectTypearr: [],

                aaa: "",
                form: {},
                objArr: [],
                options: [],

                dataobj: {},
                dataArr: []
            };
        },
        methods: {
            closeDialog() {
                this.$emit("update:show", false);
            },
            clearValidate() {
                this.$refs.ruleForm.clearValidate();
            },
            //检查后添加
            submitForm() {
                let arr = [];
                this.objectTypearr.forEach(item => {
                    if (item.isGood != -1) {
                        let obj = {
                            planId: this.editId,
                            objectType: this.objectType,
                            punish: item.isGood == 1 ? "" : item.punish.join(","),
                            checkValue: item.checkValue,
                            isGood: item.isGood
                        };

                        arr.push(obj);
                    }
                });
                apiCheckResultAdd(arr).then(res => {
                    this.closeDialog();
                    this.$message({
                        type: "success",
                        message: "添加成功!"
                    });
                });
            },
            //检查单位
            remoteMethod() {
                apiOrgSearch({keyWord: ""}).then(res => {
                    this.options = res.data;
                });
            },

            //弹框弹出时获取data给form
            initPage() {
                this.remoteMethod();
                //检查对象类型下拉
                this.$until.workBookApi("checkObjectType").then(res => {
                    this.objArr = res;
                });
                if (this.editId) {
                    //    编辑get接口
                    apiCheckResultGet(this.editId).then(res => {
                        if (res.data) {
                            res.data.forEach(item => {
                                item.isGood = String(item.isGood);
                                if (item.punish != "") {
                                    item.punish = item.punish.split(",").filter(item => item); //去除数组里面空值
                                } else {
                                    item.punish = [];
                                }
                            });
                            this.objectTypearr = res.data;
                        }
                        this.loading = false;
                    });
                } else {
                    this.loading = false;
                    this.form.objectType = "";
                    this.form.objectName = "";
                    this.form.objectTel = "";
                    this.form.objectAddress = "";
                    this.form.isCertificates = "";
                    this.form.checkUnit = "";
                    this.form.checkFreq = "";
                    this.form.remarks = "";
                }
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        },
        activated() {
            this.initPage();
        }
    };
</script>
<style lang='less' scoped>
    .checkwrop {
        padding: 10px;
        box-sizing: border-box;
    }

    .plan {
        width: 100%;
        box-sizing: border-box;

        .objecttype {
            p {
                color: gray;
                padding: 10px;

                span {
                    color: #409eff;
                    font-weight: bold;
                }
            }

            width: 100%;
            margin-top: 10px;
            border-radius: 5px;
        }
    }

    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }

    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }

    .widthselect {
        width: 100%;
    }
</style>
<style>
    .el-collapse-item {
        margin-top: 20px;
        border-radius: 8px;
        border: 1px solid #cdcccc;
        box-shadow: 0px 1px 5px #cdcccc;
        padding-left: 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 100%;
    }

    .wroptable {
        margin-top: 10px;
    }

    .check-content {
        color: green;
        font-weight: bold;
    }
</style>



